import React from "react";
import AnimatedPage from "../Components/AnimatedPage";

function About() {
  return (
    <AnimatedPage>
      <div className="about">
        <span style={{ display: "block", margin: "15px" }}>Adabiy.uz</span>
        <p>
          O'zbek tilini kirilchadan lotinga tarjima qiladigan va uni orfografik
          yoki punktuatsion hatolarga tekshiradigan ilova.
        </p>
        <span style={{ display: "block", margin: "15px" }}>
          Nigma jamoasidan tuzilgan
        </span>
      </div>
    </AnimatedPage>
  );
}

export default About;
