import styled from "styled-components";
import { NavLink as Link } from "react-router-dom";

export const Nav = styled.nav`
  background: #0b203b;
  height: 70px;
  display: flex;
  justify-content: center;
  padding: 0.5rem calc((100vh- 1000px) / 2);
  z-index: 10;
  @import url(//db.onlinewebfonts.com/c/f6c9f5fa5b05e0e1847d566236e2731f?family=Elektra+Text+Pro);
`;

export const NavLink = styled(Link)`
  color: #fff;
  display: flex;
  aling-items: center;
  text-decoration: none;
  padding: 10px 1rem;
  height: 100%;
  cursor: pointer;
  margin-right: 50px;
  transition: border-bottom 0.3s;
  &.active {
    border-bottom: 1px solid #c5e7e2;
  }
`;

export const NavMenu = styled.div`
  display: flex;
  margin: 10px auto;
`;
