import React from "react";
import "./Function.css";
function Form(props) {
  return (
    <div className="parent">
      <h1 className="title">{props.title}</h1>
      <textarea
        className={props.className}
        disabled={props.disabled}
        value={props.value}
        onChange={(e) => props.onChange(e.target.value)}
      />
    </div>
  );
}

export default Form;
