import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import AnimatedPage from "../Components/AnimatedPage";
import {
  transliterateToKiril,
  transliterateToLatin,
} from "../Components/Converter";
import Form from "../Components/Functions";

function HomeScreen() {
  const [lotin, setLotin] = useState("");
  const [kiril, setKiril] = useState("");
  const [response, setResponse] = useState([]);
  const [valueArray, setValueArray] = useState("");
  const [flag, setFlag] = useState(false);
  const [hide, setHide] = useState(2);
  const [isCheck, setIsCheck] = useState(null);

  let highlight = [];
  let copyText = "";
  let counter = -1;
  useEffect(() => {
    if (!flag) {
      setLotin(transliterateToLatin(kiril));
    } else {
      setKiril(transliterateToKiril(lotin));
    }
  }, [kiril, lotin, flag]);
  const SubmitHandler = (e) => {
    e.preventDefault();
    const wordArray = kiril.split(" ");
    if (isCheck) {
      async function getCheckedWords(words) {
        const { data } = await axios.post("http://localhost:8000/check/", {
          data: words,
        });
        setResponse(data);
        console.log(response);
      }

      setValueArray(wordArray);
      getCheckedWords(wordArray);
    } else {
      const getPunctuation = async (text) => {
        const { data } = await axios.post(
          "http://localhost:8000/punctuation/",
          {
            text,
          }
        );
        setResponse(data.split(" "));
      };
      getPunctuation(kiril);
      setValueArray(wordArray);
    }
  };

  const ClickHandler = () => {
    setHide(hide + 1);
  };

  const SwapHandler = (word, ind) => {
    let tempArray = valueArray;
    tempArray[ind] = word;
    setValueArray(tempArray);
    setHide(hide + 1);
  };

  const BtnHandler = () => {
    console.log(valueArray);
    let tempArr = "";
    for (let i of valueArray) {
      tempArr = tempArr + ` ${i}`;
    }
    console.log(tempArr);
    setKiril(tempArr);
    setFlag(true);
    setLotin(transliterateToLatin(tempArr));
    setResponse([]);
  };

  const changeLitHandler = () => {
    if (flag === true) {
      setFlag(false);
    } else {
      setFlag(true);
    }
  };

  return (
    <AnimatedPage>
      <div className="main">
        {response.length !== 0 ? (
          isCheck ? (
            <AnimatedPage>
              <div>
                <div className="txt-area show">
                  {valueArray.map((val, ind) => {
                    counter += 1;
                    copyText = copyText + ` ${val}`;
                    return (
                      <div className="display">
                        <span
                          key={Math.random()}
                          className="result"
                          onClick={ClickHandler}
                        >
                          {val}
                          <ul className="ul">
                            {response[counter].map((x) => {
                              if (val !== x) {
                                return (
                                  <li
                                    key={Math.random()}
                                    className="suggestion"
                                    onClick={(e) => SwapHandler(x, ind)}
                                  >
                                    {x}
                                  </li>
                                );
                              }
                            })}
                          </ul>
                        </span>
                      </div>
                    );
                  })}
                </div>
                <div>
                  <button className="btn" onClick={BtnHandler}>
                    Orqaga
                  </button>
                  <i
                    className="fas fa-copy cpy"
                    onClick={() => {
                      navigator.clipboard.writeText(copyText);
                    }}
                  ></i>
                </div>
              </div>
            </AnimatedPage>
          ) : (
            <AnimatedPage>
              <div className="txt-area show">
                {valueArray.map((val, ind) => {
                  counter += 1;
                  copyText = copyText + ` ${val}`;
                  return (
                    <div className="display">
                      <span
                        key={Math.random()}
                        className="result"
                        onClick={ClickHandler}
                      >
                        {response.map((x) => {
                          if (`${val},` === x || `${val}.` === x) {
                            highlight.push(x);
                            return (
                              <Fragment>
                                <ul className="ul punc">
                                  <li
                                    key={Math.random()}
                                    className="suggestion"
                                    onClick={(e) => SwapHandler(x, ind)}
                                  >
                                    {x}
                                  </li>
                                </ul>
                              </Fragment>
                            );
                          }
                        })}
                        {highlight.includes(`${val},`) ||
                        highlight.includes(`${val}.`) ? (
                          <span className="hl"> {val}</span>
                        ) : (
                          val
                        )}
                      </span>
                    </div>
                  );
                })}
              </div>
              <div>
                <button className="btn" onClick={BtnHandler}>
                  Orqaga
                </button>
                <i
                  className="fas fa-copy cpy"
                  onClick={() => {
                    navigator.clipboard.writeText(copyText);
                  }}
                ></i>
              </div>
            </AnimatedPage>
          )
        ) : (
          <div style={{ marginLeft: "8rem" }}>
            {flag === false ? (
              <div>
                <form onSubmit={SubmitHandler}>
                  <div style={{ display: "flex" }}>
                    <Form
                      title="Kiril"
                      className="txt-area"
                      value={kiril}
                      onChange={setKiril}
                    />
                    <i
                      className="fa-solid fa-arrow-right-arrow-left arrows fa-lg"
                      onClick={changeLitHandler}
                    ></i>
                    <Form
                      title="Lotin"
                      className="txt-area"
                      disabled
                      value={lotin}
                      onChange={setLotin}
                    />
                  </div>
                  <div style={{ marginLeft: "29vw", marginTop: "10px" }}>
                    <input
                      type="submit"
                      value="Imlo"
                      onClick={() => setIsCheck(true)}
                      style={{ width: "120px" }}
                    />
                    <input
                      type="submit"
                      value="Tinish"
                      style={{
                        marginLeft: "10px",
                        textAlign: "center",
                        width: "120px",
                      }}
                      onClick={() => setIsCheck(false)}
                    />
                  </div>
                </form>
              </div>
            ) : (
              <div>
                <form onSubmit={SubmitHandler}>
                  <div style={{ display: "flex" }}>
                    <Form
                      title="Lotin"
                      className="txt-area"
                      value={lotin}
                      onChange={setLotin}
                    />
                    <i
                      className="fa-solid fa-arrow-right-arrow-left arrows fa-lg"
                      onClick={changeLitHandler}
                    ></i>
                    <Form
                      title="Kiril"
                      className="txt-area"
                      value={kiril}
                      disabled
                      onChange={setKiril}
                    />
                  </div>
                  <div style={{ marginLeft: "29vw", marginTop: "10px" }}>
                    <input
                      type="submit"
                      value="Imlo"
                      style={{ width: "120px" }}
                      onClick={() => setIsCheck(true)}
                    />
                    <input
                      type="submit"
                      value="Tinish"
                      style={{
                        marginLeft: "10px",
                        textAlign: "center",
                        width: "120px",
                      }}
                      onClick={() => setIsCheck(false)}
                    />
                  </div>
                </form>
              </div>
            )}
          </div>
        )}
      </div>
    </AnimatedPage>
  );
}

export default HomeScreen;
